import Vuex from 'vuex'
import Vue from 'vue'
import mblog from '../data/blogs'
import mfmovie from '../data/movie'

Vue.use(Vuex);

const actions = {
    get_blogs(content) {
        content.state.blogs = mblog.blogs
    },
    get_movie(content) {
        content.state.movie = mfmovie.movie
    }
}

const mutations= {

}

const state = {
    blogs: [],
    movie: []
}

const getters = {

}

export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})